<template>
  <v-card>
    <v-form>
      <div class="d-flex justify-center text-h4"> </div>
      <v-container>
        <v-row class="ml-15 mr-15">
          <v-col>
            <v-select
              label=""
              v-model="registerThemeForm.cafeId"
              :items="cafes"
              item-text="name"
              item-value="cafeId"
            ></v-select>
          </v-col>
          <v-col>
            <v-select
              multiple
              label=""
              v-model="registerThemeForm.genreList"
              :items="genres"
              item-text="genre"
              item-value="genre"
            ></v-select>
          </v-col>
          <v-col>
            <v-text-field
              v-model="registerThemeForm.name"
              label=" "
              required
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="ml-15 mr-15">
          <v-col>
            <v-text-field
              v-model="registerThemeForm.time"
              label=""
              type="number"
              min="0"
              required
              placeholder="  ."
            ></v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              v-model="registerThemeForm.difficulty"
              label=""
              type="number"
              step="0.5"
              min="0"
              max="5"
              required
              placeholder=" .5  ."
            ></v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              v-model="registerThemeForm.minPlayerCount"
              label=" "
              type="number"
              min="0"
              required
            ></v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              v-model="registerThemeForm.maxPlayerCount"
              label=" "
              type="number"
              min="0"
              required
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="ml-15 mr-15">
          <v-col>
            <ImageInput
              label="  "
              @multipart-image="updateImage"
            ></ImageInput>
          </v-col>
        </v-row>
        <v-row class="ml-15 mr-15">
          <v-textarea
            v-model="registerThemeForm.description"
            label=" "
            counter="number"
            hint=" 300"
          ></v-textarea>
        </v-row>
        <v-row>
          <v-col>
            <v-btn block color="primary" @click="registerTheme"></v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
  </v-card>
</template>

<script>
import ImageInput from "@/components/ImageInput.vue";
import { IMAGE_TYPE } from "@/constants/cafe";
import { getCafeNames, getGenres, postTheme, uploadImage } from "@/api/api";
import { THEME } from "@/constants/theme";

export default {
  components: {
    ImageInput,
  },
  data() {
    return {
      multipartImage: null,
      registerThemeForm: {
        cafeId: 0,
        name: "",
        description: "",
        imageUrl: "",
        time: 60,
        minPlayerCount: 2,
        maxPlayerCount: 6,
        difficulty: 3.0,
        ageLimit: "",
        genreList: [],
      },
      cafes: [],
      genres: [],
    };
  },
  methods: {
    alert(message, color = "red") {
      this.$store.commit("snackbar/showMessage", {
        message: message,
        color: color,
      });
    },
    async registerTheme() {
      console.log(this.registerThemeForm);
      if (!this.multipartImage) {
        this.alert(IMAGE_TYPE.ERROR_MESSAGE);
        return;
      }
      if (this.registerThemeForm.cafeId === 0) {
        this.alert(THEME.MESSAGE.CAFE_NOT_SELECTED);
        return;
      }
      this.registerThemeForm.imageUrl = await uploadImage(
        this.multipartImage,
        IMAGE_TYPE.THEME
      );
      const success = await postTheme(this.registerThemeForm);
      console.log(success);
      if (success) {
        this.alert(THEME.MESSAGE.REGISTER_SUCCESS, "green");
      } else {
        this.alert(THEME.MESSAGE.REGISTER_FAIL);
      }
    },
    updateImage(multipartImage) {
      this.multipartImage = multipartImage;
    },
  },
  created() {
    getCafeNames().then((res) => {
      this.cafes = res.data.data;
    });
    getGenres().then((res) => {
      this.genres = res.data.data;
    });
    console.log(this.cafes);
    console.log(this.genres);
  },
};
</script>

<style scoped></style>
